<template>
    <ul class="locale-changer nav-bar__menu">
        <li>
            <button class="locale-changer__current-lang">
                <img :src="`${public_path}img/language/${$i18n.locale}.png`" alt="" class="locale-changer__icon">
            </button>
        </li>
        <ul class="locale-changer__lang-list">
            <li v-for="(lang, i) in locale_list" :key="`lang-${i}`" :data-code="lang.code" @click="selectLocale" class="txt">
                {{ lang.text }}
            </li>
        </ul>
    </ul>
</template>

<script>
import axios from 'axios'

export default {
    name: 'LocaleChanger',
    data() {
        return {
            current_locale: '',
            russian_locale: ['az'],
            locale_list: [
                {text: 'English', code: 'en'},
                {text: 'Russia', code: 'ru'}
            ],
            public_path: process.env.BASE_URL,
        }
    },

    computed: {},

    methods: {
        async checkedUserLocale() {
            if (localStorage.getItem('locale')) {
                this.$i18n.locale = localStorage.getItem('locale')
                console.log('get localStorage')
            } else {
                await axios.get('//ipinfo.io').then(response => {
                    const user_local = response.data['country'].toLowerCase()

                    if (!this.$i18n.availableLocales.includes(user_local)) {
                        this.$i18n.locale = 'en'
                    }
                    // this.current_locale = response.data['country'].toLowerCase()
                    console.log(this.$i18n)
                    // this.$i18n.locale = response.data['country'].toLowerCase()
                })
            }
        },

        selectLocale(event) {
            const select_locale = event.target.dataset.code
            this.$i18n.locale = select_locale
        }
    },

    async mounted() {
        // console.log(this.$route)
        // this.$router.push({ query: {test: 'h'} });
        console.log('Locale: ' + this.$i18n.locale)
    }
}
</script>

<style scoped lang="scss">
.locale-changer {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 10px;

    &:hover>&__lang-list {
        display: block;
    }

    &__current-lang {
        background: none;
        border: none;
    }

    &__icon {
        width: 25px;
    }

    &__lang-list {
        background-color: var(--box-bg);
        display: none;
        position: absolute;
        top: 100%;
        left: -50%;
        border-radius: 7px;
        overflow: hidden;
        box-shadow: inset 0 2px 4px var(--box-highlight), inset 0 -2px 4px var(--box-in-shadow), 0 8px 16px var(--box-out-shadow);

        li {
            padding: 7px 10px;
            color: #fff;
            cursor: pointer;

            &:hover {
                background-color: var(--cta-bg);
                color: #111;
            }

            &.active {
                background-color: var(--cta-bg);
                color: #111;
            }
        }
    }
}
</style>